
.understrap-read-more-link {
  display: none !important;
}
.read-more-link {
  text-decoration: none;
  margin-bottom: 7px;
  margin-left: 8px;
}
.read-more-link:hover {
    text-decoration: none;
}
.news-layout {
  padding: 0 65px 0 65px;
}
@media screen and (max-width: 768px) {
.news-layout {
      padding: 0 15px 0 15px;
}
}
@media screen and (max-width: 420px) {
.news-layout {
      padding: 0;
}
}



