
.card {
  margin-top: 50px;
}
.description {
  outline: 0in;
  border: 1px solid lightblue;
}
.initiative-card {
  padding-bottom: 20px;
}
.initiative-card label.v-label.theme--light {
    margin-bottom: 0;
}
@media screen and (max-width: 322px) {
.initiative-card .delete-button {
      margin-left: 28px !important;
      margin-top: 10px;
}
}
#save-and-close-button__intiative-component {
  margin-left: 31px;
}
@media screen and (max-width: 420px) {
#save-and-close-button__intiative-component {
      margin-left: 28px;
}
}
@media screen and (max-width: 320px) {
.v-btn__content {
    font-size: 9px !important;
}
}



