
.card {
  margin-top: 50px;
}
.bugs .header {
  margin-top: 10px;
}
.bugs .btnHeader {
  width: 4.5em;
  height: 2em;
  float: right;
  margin-top: 5px;
}
.extra-small-button {
  width: 30px !important;
  height: 30px !important;
}
.extra-small-button:focus {
    outline: none;
}
.extra-small-button i {
    font-size: 15px;
}
.v-toolbar--card .checkbox {
  margin-top: 15px;
}
.v-toolbar--card .checkbox label {
    margin-bottom: 0;
}
.cards-toolbar {
  height: auto !important;
}
@media screen and (max-width: 1264px) {
.cards-toolbar .v-btn {
      margin: 6px 4px;
}
}
.cards-toolbar .clear-filter-botton {
    margin-bottom: 13px;
}
@media screen and (max-width: 1264px) {
.cards-toolbar .clear-filter-botton {
        margin-bottom: 13px;
}
}
.save-and-close-button {
  margin-left: 14px !important;
}
@media screen and (max-width: 420px) {
.save-and-close-button {
      margin-left: 9px !important;
}
}
@media screen and (max-width: 420px) {
.new-bug-subheader {
    padding-left: 0 !important;
}
}
.padding-left-sm {
  padding-left: 12px;
}
@media screen and (max-width: 420px) {
.padding-left-sm {
      padding-left: 0;
}
}



